import React, {useEffect} from 'react';
import logoT from '../assets/MoMoWorksLogo.png';
import logoM from '../assets/0xgpt_logoF.svg';
import AOS from 'aos';
import 'aos/dist/aos.css';
import TwitterIcon from '../assets/twitter.svg';
import TelegramIcon from '../assets/telegram.svg';
import MediumIcon from '../assets/medium.svg';
import { Link } from 'react-router-dom';
import TelegramIcon2 from '@mui/icons-material/Telegram';
import { useGlitch } from 'react-powerglitch'
import { PowerGlitch } from 'powerglitch'; 
import Noise from './NoiseDittering';
import { keyframes } from "@emotion/react";
import { Reveal } from "react-awesome-reveal";
import { Slide } from "react-awesome-reveal";

const Topbar = () => {

    const customAnimation = keyframes`
  from {
    opacity: 0;

  }

  to {
    opacity: 1;
    
  }
`;
   
    const glitch = useGlitch(
        {
            "playMode": "hover",
            "createContainers": true,
            "hideOverflow": false,
            "timing": {
              "duration": 250,
              "iterations": 6
            },
            "glitchTimeSpan": {
              "start": 0,
              "end": 1
            },
            "shake": {
              "velocity": 15,
              "amplitudeX": 0.2,
              "amplitudeY": 0.2
            },
            "slice": {
              "count": 6,
              "velocity": 15,
              "minHeight": 0.02,
              "maxHeight": 0.15,
              "hueRotate": true
            },
            "pulse": false
          }
        );
       


        
    useEffect(() => {
        AOS.init({duration: 1000});
      }, [])

     
      
    return ( 
        <div>
            <div>
           <div className='topbar'  data-aos="fade-down" >
           <Reveal delay={500} duration={2000} keyframes={customAnimation}><div><img className='topLogo'ref={glitch.ref}   src={logoT} alt='logo' /></div></Reveal>
           
           <Link to='https://t.me/MoMo_Works' target='blank_'>
           
           <button  ref={glitch.ref} className='topbutton'>Write me<TelegramIcon2 style={{marginLeft:'0.2em'}} /></button></Link>
            <Noise/>
            
           </div>
           
           </div>
        </div>
    ); 
};

export default Topbar;